import React, { useState } from "react";
import axios from "axios";
import swal from "sweetalert";
import { API } from "../../config";
import FormBase from "../components/FormBase/FormBase";
import FormHeader from "../components/FormHeader/FormHeader";
import FormInput from "../components/FormInput/FormInput";
import FormSubmit from "../components/FormSubmit/FormSubmit";
import FormFooter from "../components/FormFooter/FormFooter";
import classNames from "./Login.module.css";
import FormLoader from "../components/FormLoader/FormLoader";
import { NavLink } from "react-router-dom";

const Login = ({ history }) => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);

  const loginUser = () => {
    setLoading(true);
    console.log(password);
    axios
      .post(`${API}/login`, {
        email: email,
        password: password,
      })
      .then((response) => {
        setLoading(false);
        localStorage.setItem("authToken", JSON.stringify(response.data.token));
        localStorage.setItem("user", JSON.stringify(response.data.data.user));
        swal(
          "Login success!",
          "You will now redirect to the App page!",
          "success",
          {
            button: "Okay",
          }
        ).then(() => {
          history.push(`${process.env.PUBLIC_URL}/app`);
        });
      })
      .catch((err) => {
        setLoading(false);
        // console.log(err.response);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  return (
    <div className={classNames.container}>
      <FormBase>
        <FormHeader title="Login" />
        <div className={classNames.inputs_container}>
          <FormInput
            i={{
              id: "email",
              type: "email",
              placeholder: "Enter your email",
              label: "Your e-mail",
              setFn: setEmail,
            }}
          />
          <FormInput
            i={{
              id: "password",
              type: "password",
              placeholder: "********",
              label: "Password",
              setFn: setPassword,
            }}
          />
          {loading ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Login" clickFn={loginUser} />
          )}
        </div>
        <FormFooter
          d={{
            title: "Don't have an account?",
            btn: "Signup",
            btn_link: "/signup",
          }}
        />
        <div className={classNames.forget_password}>
          <p>
            Forgot password ?{" "}
            <NavLink to={`${process.env.PUBLIC_URL}/forget-password`}>
              Click here to reset password
            </NavLink>
          </p>
        </div>
      </FormBase>
    </div>
  );
};

export default Login;
