import React from "react";
import classNames from "./FormFooter.module.css";
import { NavLink } from "react-router-dom";

const FormFooter = ({ d }) => {
  return (
    <div className={classNames.container}>
      <p>{d.title}</p>
      <NavLink to={`${process.env.PUBLIC_URL}/${d.btn_link}`}>{d.btn}</NavLink>
    </div>
  );
};

export default FormFooter;
